.Standard-Post{
  width:96vw;
  margin-left:2vw;
  margin-right:2vw;
}

.Expanded-Post{
  width:100vw;
  margin:0;
}

.attendeesList{
  display:-ms-flexbox;
  display:flex;
  list-style-type: none;
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding: 0;
  margin-bottom:0;
}
.attendeesObject{
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction:column;
      flex-direction:column;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left:10px;
  margin-right:10px;
}
.avatar{
  margin-left: auto;
  margin-right:auto;
}
.IconButton{
  right:0;
  margin:0;
  position:relative;
  float:right;
}

.upcoming{
	width: 50%;
	height: 40px;
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	padding-top: 15px;
}
	

.selected{
	background-color: #45cbde;
	color: white;
	-webkit-text-decoration: bold;
	        text-decoration: bold;
}



